import React, { Component } from 'react'
import { IncreaseChart } from './charts'
import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import DATA from './data'
import './index.scss'

class App extends Component {
  state = DATA

  componentWillMount () {
    const url = getDomain('/mem/v1/mem?action=memStatistics&clientType=2')

    fetchUtil.getJSON(url).then(data => {
      this.setState(data)
    })['catch'](e => {

    })
  }

  render () {
    const { increase, active, charge, all, analysis: { access, order, payment } } = this.state
    const orderScale = (((order.new + order.old) / (access.new + access.old)) * 100).toFixed(0)
    const payScale = (((payment.new + payment.old) / (order.new + order.old)) * 100).toFixed(0)
    return (
      <div className="m-container">
        <div className="content">
          <ul className="chart-list">
            <li className="item">
              <div className="data pull-left">
                <dl className="category">
                  <dt></dt>
                  <dd>{increase.total}</dd>
                </dl>
                <p className="text"><span>{increase.yesterday}</span></p>
                <p className="text"><span>{increase.week}</span></p>
                <p className="text"><span>{increase.month}</span></p>
              </div>
              <div className="chartImg pull-right">
                <IncreaseChart data={increase.list} />
              </div>
            </li>
            <li className="item">
              <div className="data pull-left">
                <dl className="category">
                  <dt></dt>
                  <dd>{active.total}</dd>
                </dl>
                <p className="text"><span>{active.yesterday}</span></p>
                <p className="text"><span>{active.week}</span></p>
                <p className="text"><span>{active.month}</span></p>
              </div>
              <div className="chartImg pull-right">
                <IncreaseChart data={active.list} />
              </div>
            </li>
            <li className="item">
              <div className="data pull-left">
                <dl className="category">
                  <dt></dt>
                  <dd>{charge.total}</dd>
                </dl>
                <p className="text"><span>{charge.yesterday}</span></p>
                <p className="text"><span>{charge.week}</span></p>
                <p className="text"><span>{charge.month}</span></p>
              </div>
              <div className="chartImg pull-right">
                <IncreaseChart data={charge.list} />
              </div>
            </li>
            <li className="item">
              <div className="data pull-left">
                <dl className="category">
                  <dt></dt>
                  <dd>{all.total}</dd>
                </dl>
                <p className="text"><span>{all.yesterday}</span></p>
                <p className="text"><span>{all.week}</span></p>
                <p className="text"><span>{all.month}</span></p>
              </div>
              <div className="chartImg pull-right">
                <IncreaseChart data={all.list} />
              </div>
            </li>
          </ul>
          <div className="data-analysis">
            <h3 className="title"></h3>
            <div className="analysis-left">
              <div className="analysis1-content">
                <span className="analysis-btn1"><i className="iconfont icon-access" /></span>
                <p className="analysis-text">:{access.new + access.old}
                  <span>:{access.new}:{access.old}</span>
                </p>
              </div>
              <div className="analysis1-content">
                <span className="analysis-btn2"><i className="iconfont icon-shoppingcar" /></span>
                <p className="analysis-text">:{order.new + order.old}
                  <span>:{order.new}:{order.old}</span>
                </p>
              </div>
              <div className="analysis1-content">
                <span className="analysis-btn3"><i className="iconfont icon-pay" /></span>
                <p className="analysis-text">
                  :{payment.new + payment.old}                  
                  <span>:{payment.new}:{payment.old}</span>
                </p>
              </div>
              <div className="analysis-link1" />
              <div className="analysis-link2" />
            </div>
            <div className="analysis-right">
              <div className="analysis2-content1">
                <div className="analysis2-box">
                  <span className="analysis2-text1"></span>
                  <span className="analysis2-text2">{orderScale}<i>%</i></span>
                </div>
              </div>
              <div className="analysis2-content2">
                <div className="analysis2-box">
                  <span className="analysis2-text1"></span>
                  <span className="analysis2-text2">{payScale}<i>%</i></span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default App



